*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
.content{
  height: 100vh;
}
.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 101vh;
  background-color: #344955;
}
.form{
  border-radius: 19px;
  padding: 41px;
  background-color:#80C4E9;
}
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}
.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon{
  color: black;
}
.inactive-icon{
  color: gray;
}
.root{
  background-color: #987D9A;
  padding: 5px;
  padding-bottom: 297px;
}
.login-page{
  width: 338px;
    margin-left: 592px;
    margin-top: 234px;
    background-color: #EECEB9;
    border-radius: 19px;
}
.login{
  padding: 21px;
}
.LOGIN{
  background-color: rgb(99 101 157 / 81%);
  padding-top: 2px;
  padding-bottom: 209px;
}
.navbar{
  padding-top: 0px;
  padding-bottom: 0px;
}
.container-fluid{
  background-color: rgb(68 91 188 / 99%);
}
.nav-item{
  padding-right: 20px;
  margin-top: 13px;
}
.nav-link{
  background-color: floralwhite;
    border-radius: 39px;
    padding: 7px 16px;
}
.filters{
  background-color: #afb9b5;
}
.select{
  background-color: #afb9b5;
}